.header-main {
    color: var(--font-color);
    height: 100vh;
    width: 100%;

    display: flex;
    justify-content: center;
}

.header-content {
    width: 100%;

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

.header-text {
    background: transparent;
    z-index: 1;
    text-align: center;
    font-size: 1.5em;
    margin-bottom: 3rem;
}

.header-text h4, span {
    background: transparent;
}

.header-text span {
    color: var(--contrast-color);
}

.home-menu-container, .menu-visible {
    z-index: 1;
}

.header-tag {
    z-index: 1;
    margin-top: 15rem;
    background: transparent;
}

.header-tag a, .header-tag a:visited {
    text-decoration: none;
    color: var(--contrast-color);
}

@media screen and (min-width: 769px) {
    .header-text {
        font-size: 1.6em;
    }
}

@media screen and (min-width: 1025px) {
    .header-text {
        font-size: 1.65em;
    }
}

@media screen and (min-width: 1201px) {
    .header-text {
        font-size: 1.7em;
    }
}